*{margin: 0;padding: 0; text-align: center;}

.black-nav {background-color: #000; color: #fff;  padding : 20px;}
.list {
  text-align : left;
  padding : 20px;
  border-bottom : 1px solid grey;
  display: flex;
  flex-direction: column;
  align-items: center; /* 수직 중앙 정렬 */
  margin-bottom: 10px; /* 각 리스트 아이템 사이의 간격 조정 */
}
.list button {
  background-color: rgb(176, 82, 82);
  margin-top: 10px; /* 가방 삭제 버튼과 텍스트 사이의 간격 조정 */
}
button {margin-top: 30px; border:none; background-color: #000; padding: 10px 20px; border-radius: 5px; color: #fff;}